import React, { useState } from 'react'
import styled from 'styled-components'
import { NavLink, useLocation, useHistory, Redirect } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { saveAllCandidates, saveCurrentCandidate } from '../Actions/CandidateActions'
import { saveAllJobs, saveCurrentJob } from '../Actions/JobsActions'

import DashboardSVG from '../Assets/Icons/dashboard.svg'
import ProfileSVG from '../Assets/Icons/profile.svg'
import JobSVG from '../Assets/Icons/job-dashboard.svg'
import CandidateSVG from '../Assets/Icons/candidate.svg'


const activeStyles = {
    backgroundColor: '#f1f1f1'
}

function SideBar() {
    const dispatch = useDispatch()
    const [page, setPage] = useState(1)



    return (
        <SidebarContainer>


            <Routes activeStyle={page === 1 ? activeStyles : {}} onClick={() => setPage(1)} to='/'>
                <RouteIcon src={DashboardSVG} />
                <RouteText>Dashboard</RouteText>
            </Routes>
            <Routes activeStyle={page === 2 ? activeStyles : {}} onClick={() => setPage(2)} to='/jobs'>
                <RouteIcon src={JobSVG} />
                <RouteText>Jobs</RouteText>
            </Routes>
            <Routes activeStyle={page === 3 ? activeStyles : {}} onClick={() => setPage(3)} to='/candidates'>
                <RouteIcon src={CandidateSVG} />
                <RouteText>Candidates</RouteText>
            </Routes>
            <Routes activeStyle={page === 4 ? activeStyles : {}} onClick={() => setPage(4)} to='/profile'>
                <RouteIcon src={ProfileSVG} />
                <RouteText>Profile</RouteText>
            </Routes>

        </SidebarContainer>
    )
}

export default SideBar

const SidebarContainer = styled.div`
    background-color: white;
    width: 15%;
    display: flex;
    flex-direction: column;
`

const RouteContainer = styled.div`
    display: flex;
`

const RouteIcon = styled.img`
    margin-right: 10px;
`
const RouteText = styled.p`
    margin: 0px;
    padding: 0px;
    
`

const Routes = styled(NavLink)`
    display: flex;
    text-decoration: none;
    color: grey;
    font-weight: bold;
    text-align: left;
    padding: 10px 20px;
    &:hover{
        
        text-decoration: none;
        background-color: white;
    }

    &:active{
        text-decoration: none;
    }

    

    

`