import React, { useState } from 'react'
import { Card, themeColor, StyledButton } from '../Common/Styles/StyledComponents'
import styled from 'styled-components'
import { useDispatch, useSelector } from 'react-redux'
import axios from 'axios'
import { API_URI } from '../Endpoint'
import { login } from '../Actions/AuthActions'
import CompanyLogo from '../Assets/Icons/fistbump-logo.svg'
import User from '../Assets/Icons/user.svg'
import Password from '../Assets/Icons/password.svg'

function SignInPage() {
    const dispatch = useDispatch()
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const { isAuthenticated } = useSelector(state => state.authData)
    console.log(isAuthenticated)

    const loginHandler = async (e) => {
        e.preventDefault()
        const URL = `${API_URI}login`

        try {
            const data = {
                name: username,
                password
            }

            const loginData = await axios.post(URL, data)
            dispatch(login(true))
        } catch (err) {
            dispatch(login(false))
        }
    }
    return (
        <SiginContainer>

            <SigninSubContainer>
                <div>
                    <BrandLogo src={CompanyLogo} />
                </div>
                <SigninCard>
                    <SiginTitle>Admin</SiginTitle>
                    {/* <SiginSubTitle>to Admin Portal</SiginSubTitle> */}
                    <SigninForm onSubmit={loginHandler}>
                        <InputContainer>
                            <Icon src={User} />
                            <Input value={username} onChange={(e) => setUsername(e.target.value)} type="text" placeholder="Username" />
                        </InputContainer>

                        <InputContainer>
                            <Icon src={Password} />
                            <Input value={password} onChange={(e) => setPassword(e.target.value)} type="password" placeholder="Password" />
                        </InputContainer>
                        <Login>Login</Login>
                    </SigninForm>
                </SigninCard>

            </SigninSubContainer>
        </SiginContainer>
    )
}

export default SignInPage


const SiginTitle = styled.h2`
    color: ${themeColor};
    margin: 0px;
    padding: 0px;
    font-weight: bold;
    margin-bottom: 20px;
    `
const SiginSubTitle = styled.h5`
    color: ${themeColor};
    margin: 0px;
    padding: 0px;
`

const SiginContainer = styled.div`
    background-color: #f4f4f4;
    height: 100vh;
`

const BrandLogo = styled.img`
    width: 250px;
    margin-bottom: 20px;
`

const SigninSubContainer = styled.div`
padding-top: 70px;
    /* display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; */
`

const SigninCard = styled(Card)`
    margin: auto;
   
    width:300px;
    /* height:300px; */
`

const SigninForm = styled.form`
    display: flex;
    flex-direction: column;
`


const Login = styled.button`
    ${StyledButton}
    width: 100%;
    margin: 10px 0px;
    padding: 6px 12px;
    /* margin: 10px 0px;
    padding: 6px 12px;
    background-color: #276eb0;
    border-style: none;
    color: white;
    font-weight: bold; */

`
const Icon = styled.img`
    margin-right: 10px;
`

const InputContainer = styled.div`
    width: 100%;
    display: flex;
`

const Input = styled.input`
width: 100%;
    margin: 10px 0px;
    padding: 6px 12px;
    border-style: none;
    border-bottom: 1px solid #0000001f;
    transition: all 0.5s;
    &:focus{
        outline: none;
        border-bottom: 1px solid ${themeColor};
    }
`
